.navigation {
  overflow: hidden;
  position: absolute;
  right: 15px;
  top: 15px;
  width: auto;
  height: 50px;
  background: #fff; }

.navigation__check {
  z-index: -1000;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  pointer-events: none; }

.navigation__content {
  position: relative;
  width: 50px;
  height: 100%;
  transition: width 1s cubic-bezier(0.49, -0.3, 0.68, 1.23); }

.navigation__check:checked ~ .navigation__content {
  transition: width 1s cubic-bezier(0.48, 0.43, 0.29, 1.3);
  width: 300px; }

.navigation__list {
  display: flex;
  position: relative;
  width: 300px;
  height: 100%;
  padding-left: 10px;
  padding-right: 50px;
  list-style-type: none;
  font-size: 0;
  background-color: #FF5722;
  border-radius: 5px;
  border: 1px solid #ffffff; }

.navigation__link {
  padding: 0 7px;
  display: block;
  height: 100%;
  transform: rotateY(-70deg);
  opacity: 0;
  text-decoration: none;
  color: #ffffff;
  font-size: 14px;
  line-height: 50px;
  font-weight: 700;
  perspective: 1000px;
  transition: transform 0.8s cubic-bezier(0.48, 0.43, 0.7, 2.5), opacity 0.8s; }
  .navigation__check:checked ~ .navigation__content .navigation__link {
    transform: rotateY(0);
    opacity: 1;
    transition: transform 0.8s cubic-bezier(0.48, 0.43, 0.7, 2.5), opacity 0.2s; }
  .navigation__item:nth-child(1) .navigation__link {
    transition-delay: 0.2s; }
  .navigation__check:checked ~ .navigation__content .navigation__item:nth-child(1) .navigation__link {
    transition-delay: 0s; }
  .navigation__item:nth-child(2) .navigation__link {
    transition-delay: 0.1s; }
  .navigation__check:checked ~ .navigation__content .navigation__item:nth-child(2) .navigation__link {
    transition-delay: 0.1s; }
  .navigation__item:nth-child(3) .navigation__link {
    transition-delay: 0s; }
  .navigation__check:checked ~ .navigation__content .navigation__item:nth-child(3) .navigation__link {
    transition-delay: 0.2s; }

.navigation__btn {
  position: absolute;
  right: 0;
  top: 0;
  width: 50px;
  height: 50px;
  padding: 16px 11px;
  cursor: pointer;
  background-color: #ffffff; }
  .navigation__btn:before, .navigation__btn:after {
    content: "";
    display: block;
    width: 28px;
    height: 6px;
    border-radius: 2px;
    background: #0d47a1;
    transform-origin: 50% 50%;
    transition: transform 1s cubic-bezier(0.48, 0.43, 0.29, 1.3), background-color 0.3s; }
  .navigation__btn:before {
    margin-bottom: 6px; }
  .navigation__btn:hover:before, .navigation__btn:hover:after {
    background: #FF5722; }
  .navigation__check:checked ~ .navigation__btn:before {
    background: #FF5722;
    transform: translateY(6px) rotate(-225deg); }
  .navigation__check:checked ~ .navigation__btn:after {
    background: #FF5722;
    transform: translateY(-6px) rotate(225deg); }

@media screen and (min-width: 768px) {
  .navigation__btn {
    display: none; }
  .navigation__content {
    width: auto; }
  .navigation__list {
    padding: 0;
    width: auto;
    background-color: transparent; }
  .navigation__link {
    padding: 0 10px;
    font-size: 18px;
    font-weight: 700;
    color: #333333;
    opacity: 1;
    transform: rotateY(0); }
    .navigation__link:hover {
      background: linear-gradient(to right, #8a2387, #e94057, #f27121);
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent; } }

@media screen and (min-width: 1024px) {
  .navigation__link {
    position: relative; }
    .navigation__link:after {
      top: 0;
      left: 0;
      border-bottom: 1px solid #FF5722;
      transition: 0s ease opacity .4s, .2s ease width , .2s ease height .2s; }
    .navigation__link:hover:after {
      transition: 0s ease opacity .0s, .2s ease height .0s , .2s ease width .2s; }
  .navigation__link:after {
    content: "";
    position: absolute;
    width: 0;
    height: 0;
    opacity: 0; }
  .navigation__link:hover:after {
    height: 100%;
    width: 100%;
    opacity: 1; } }

